import React from 'react';

const Sidebar = ({ isOpen }) => {
  return (
    <aside
      className={`${
        isOpen ? 'w-64' : 'w-0'
      } bg-gray-900 text-white transition-all duration-300 overflow-hidden`}
    >
      <div className="p-6">
        <h1 className="text-2xl font-bold mb-8">Dotix Automation</h1>
        
        <nav className="space-y-2">
          <a href="/dashboard" className="flex items-center space-x-3 px-4 py-3 rounded-lg bg-gray-800 text-white">
            <span>Dashboard</span>
          </a>
          <a href="/jobs" className="flex items-center space-x-3 px-4 py-3 rounded-lg hover:bg-gray-800 text-gray-300">
            <span>Jobs</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-4 py-3 rounded-lg hover:bg-gray-800 text-gray-300">
            <span>Webhooks</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-4 py-3 rounded-lg hover:bg-gray-800 text-gray-300">
            <span>Settings</span>
          </a>
        </nav>
      </div>
    </aside>
  );
};

export default Sidebar;